@import url(https://fonts.googleapis.com/css?family=Anton|Dosis|Montserrat|Oswald|Questrial|Rajdhani|Teko);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html,body{color:white;background-color:#111;overflow-x:hidden}html body,body body{padding-bottom:5rem}html .navbar,body .navbar{font-family:Montserrat, sans-serif;background-color:#111}html .navbar.navbar-dark .navbar-toggler,body .navbar.navbar-dark .navbar-toggler{border:none}html .navbar .navbar-brand,html .navbar .nav-link,body .navbar .navbar-brand,body .navbar .nav-link{text-transform:uppercase}button:focus{outline:0px}hr{border-color:#aaa}.section{min-height:100vh;box-sizing:border-box}.section .section-header{font-size:3rem;letter-spacing:0.5rem;font-family:Rajdhani, Oswald, sans-serif}.section .section-desc{font-family:Montserrat, sans-serif}.project{background-size:100%;font-family:Rajdhani, Oswald, sans-serif;margin-bottom:4rem;overflow:hidden;text-align:center}.project .project-head,.project .project-link{color:white;text-shadow:0px 0px 5px rgba(0,0,0,0.8)}p{font-family:Rajdhani, Oswald, sans-serif}#skills{text-align:left;text-align:initial}#skills ul,#team ul{list-style:none;padding:0px;margin-bottom:0.5rem}#skills ul li,#team ul li{display:inline-block;text-transform:uppercase;padding:0.5rem 1rem}.footer{text-align:right;position:fixed;bottom:0rem;background-color:#111;right:0px;padding-right:1rem}.footer .navbar-brand{font-size:1rem;display:inline-block;color:#555}.footer ul{display:inline-block;list-style:none;padding:0px;margin-bottom:0.5rem}.footer ul li{cursor:pointer;font-size:1.5rem;padding:5px;width:2rem;text-align:center;display:inline-block}.footer ul li a{color:#aaa}.footer ul li a:hover{color:white}@media only screen and (max-width: 575px){.nav-link{padding-top:1.5rem;padding-bottom:1.5rem}.navbar-nav{height:90vh}.header,.footer{background:#111}}@media only screen and (max-width: 360px){.footer .navbar-brand{margin-right:0.5rem}}::-webkit-scrollbar{width:5px;height:5px}::-webkit-scrollbar-thumb,kbd{background:#111}::-webkit-scrollbar-track{background:white}

